import { newSpecPage } from '@stencil/core/testing';
import { PrestoDynamicOverlayUi } from '../presto-dynamic-overlay-ui';

describe('presto-dynamic-overlay-ui', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoDynamicOverlayUi],
      html: `<presto-dynamic-overlay-ui></presto-dynamic-overlay-ui>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-dynamic-overlay-ui>
        <mock:shadow-root>
          <span class="overlay--top-right overlay-text" part="overlay-text" style="font-size: 10px;">
            <slot></slot>
          </span>
        </mock:shadow-root>
      </presto-dynamic-overlay-ui>
    `);
  });
});
